<template>
    <div>
            <v-toolbar flat color="white">
                <v-toolbar-title>Crud Atma Pedia</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>

                <v-spacer></v-spacer> 

                <v-btn v-show="selected.length > 0" @click="multipleDelete()" color="red" dark class="mb-2" style="margin-right: 25px">Delete {{ selected.length }}</v-btn>
                
                <v-dialog v-model="dialog" max-width="500px">
                    <template v-slot:activator="{ on }">
                        <v-btn color="primary" dark class="mb-2" v-on="on">Tambah Makanan</v-btn>
                    </template>

                    <v-card>
                        <v-card-title>
                            <span class="headline">{{ formTitle }}</span>
                        </v-card-title>

                        <v-card-text>
                            <v-container>
                                <v-row>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field v-model="editedItem.name" label="Dessert name"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field v-model="editedItem.calories" label="Calories"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field v-model="editedItem.fat" label="Fat (g)"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field v-model="editedItem.carbs" label="Carbs (g)"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field v-model="editedItem.protein" label="Protein (g)"></v-text-field>
                                    </v-col>
                                </v-row>
                            </v-container>
                        </v-card-text>

                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                            <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>
        <v-layout>
            <v-flex md4 x512 v-for="item in desserts" :key="item['.key']">
                <v-card class="d-inline-block mx-auto" max-width="344" outlined>
                <v-list-item three-line>
                <v-list-item-content>
                    <div class="overline mb-4">Desserts</div>
                    <v-list-item-title class="headline mb-1">{{item.name}}</v-list-item-title>
                    <v-list-item-subtitle> Carbs: {{item.carbs}}</v-list-item-subtitle>
                    <v-list-item-subtitle> Protein: {{item.protein}}</v-list-item-subtitle>
                    <v-list-item-subtitle> Fat: {{item.fat}}</v-list-item-subtitle>
                    <v-list-item-subtitle> Calories: {{item.calories}}</v-list-item-subtitle>
                </v-list-item-content>
            </v-list-item>
            
            <v-card-actions>
                 <v-btn class="primary" text @click="editItem(item)">Edit</v-btn>
                <v-btn class="error" text @click="deleteItem(item)">Delete</v-btn>
            </v-card-actions>
        </v-card>
    </v-flex>
</v-layout>
</div>  
</template>

<script>
import { dessertsRef} from '../firebase';
    export default {
        data: () => ({
            cek : -1,
            dialog: false,
            headers: [{
                    text: 'Dessert (100g serving)',
                    align: 'left',
                    sortable: false,
                    value: 'name',
                },
              
                {
                    text: 'Calories',
                    value: 'calories'
                },
                {
                    text: 'Fat (g)',
                    value: 'fat'
                },
                {
                    text: 'Carbs (g)',
                    value: 'carbs'
                },
                {
                    text: 'Protein (g)',
                    value: 'protein'
                },
                  {
                text: "#",
                value: "multidelete",
                sortable: false
                },
                {
                    text: 'Actions',
                    value: 'action',
                    sortable: false
                },
            ],
            desserts: [],
            selected: [],
            editedIndex: -1,
            editedItem: {
                name: '',
                calories: 0,
                fat: 0,
                carbs: 0,
                protein: 0,
            },
            defaultItem: {
                name: '',
                calories: 0,
                fat: 0,
                carbs: 0,
                protein: 0,
            },
        }),
        firebase: {
            desserts : dessertsRef
        },
        computed: {
            formTitle() {
                return this.cek === -1 ? 'New Item' : 'Edit Item'
            },
        },

        watch: {
            dialog(val) {
                val || this.close()
            },
        },
          methods: {
        editItem (item) {
      this.editedIndex = item['.key']
      this.cek = 0
      this.editedItem = Object.assign({}, item)
      this.dialog = true
    },
    deleteItem (item) {
      confirm('Are you sure you want to delete this item?') && 
      dessertsRef.child((item['.key'])).remove()
    },

    multipleDelete() {
          this.selected.forEach(item => {
              dessertsRef.child(item).remove();
              this.selected = [];
              
          });
      },
    close () {
      this.dialog = false
    //   //setTimeout(() => {
    //     //this.editedItem = Object.assign({}, this.defaultItem)
    //     //this.editedIndex = -1
    //   }, 300)
    },
    clear(){
      this.editedItem={}
      this.editedItem.name= ''
      this.editedItem.calories= 0
      this.editedItem.fat= 0
      this.editedItem.carbs= 0
      this.editedItem.protein= 0 
      this.cek= -1
    },
    // methods untuk save
    save () {
      if (this.cek > -1) {
        dessertsRef.child(this.editedIndex).set({name: this.editedItem.name,
        calories: this.editedItem.calories,
        fat: this.editedItem.fat,
        carbs: this.editedItem.carbs,
        protein: this.editedItem.protein})
        this.clear()
      }else{
        dessertsRef.push({name: this.editedItem.name,
        calories: this.editedItem.calories,
        fat: this.editedItem.fat,
        carbs: this.editedItem.carbs,
        protein: this.editedItem.protein})
        this.clear()
      }
       this.selected = []
      this.close()
    },
  },
}
</script>